import Vue from 'vue'
import Router from 'vue-router'
import UiLogin from '@/components/UIlogin'
import mainContain from '@/components/mainContain'
import topNav from '@/components/topNav'
import footer from '@/components/footer'

import mainPage1 from '@/components/mainPage1'
import mainPage2 from '@/components/mainPage2'
import mainPage3 from '@/components/mainPage3'

import flex from '@/components/flex'

import animate from '@/components/animate'
import pubu from '@/components/pubu'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'mainPage1',
      redirect: '/mainPage1'
    },
    {
      path: '/mainContain',
      name: 'mainContain',
      component: mainContain
    },
    {
      path: '/topNav', // 头部通用
      name: 'topNav',
      component: topNav
    },
    {
      path: '/footer', // 头部通用
      name: 'footer',
      component: footer
    },
    {
      path: '/mainPage1',
      name: 'mainPage1',
      component: mainPage1
    },
    {
      path: '/mainPage2',
      name: 'mainPage2',
      component: mainPage2
    },
    {
      path: '/mainPage3',
      name: 'mainPage3',
      component: mainPage3
    },
    {
      path: '/flex',
      name: 'flex',
      component: flex
    },
    {
      path: '/animate',
      name: 'animate',
      component: animate
    },
    {
      path: '/pubu',
      name: 'pubu',
      component: pubu,
      meta:{keepAlive:true}
    }
  ]
})



